<template lang="">
  <div id='bigBox' class="txal">
      <div>
          <button @click='getData' class="Mybtn mg-bt-10">刷一下</button>
      </div>
    <div id="myChart" class="dslb" :style="{width: '100vw', height: '100vh'}"></div>
  </div>
</template>

<script>
    // import echarts from 'echarts'
    export default {
        components: {

        },
        name: 'index',
        computed: {

        },
        data() {
            return {
                message: ['happy'],
                userlist:[]
            }
        },
        created() {
            // this.$api.home({
            //     name: '001',
            // }, res => {
            //     console.log(res)
            // })
        },
        mounted() {
          this.getData()
        },
        methods: {
            getData(){
                this.$api.user({},res=>{
                    console.log(res)
                    if(res.code==1000){
                    this.userlist=res.data
                    this.drawLine(res.data)
                    }
                })
            },
            drawLine(list){

              // 基于准备好的dom，初始化echarts实例
              let myChart = echarts.init(document.getElementById('myChart'))
              let arra=list
              let arr=[]
              let linkArr=[]
              for(let i of arra){
                  arr.push({
                      name: i.name,
                      value:i.score,
                      symbolSize: i.score,
                      draggable: true,
                    //   category: i.name=='000'?1:2,
                      category: 1,
                      itemStyle: {
                          normal: {
                              borderColor: i.b_color?i.b_color:'red',
                              borderWidth: (i.score/20).toFixed(0),
                              shadowBlur: (i.score/6).toFixed(0),
                              shadowColor: i.b_color?i.b_color:'red',
                              color: i.c_color?i.c_color:'black',
                          }
                      } 
                  })
                  linkArr.push(i.name)
              }
            let linkArr0=[]
            console.log('length:'+arra.length)
            for(let j of linkArr){
                let a=linkArr[Math.floor((Math.random()*(arra.length-1))+1)]
                let b=linkArr[Math.floor((Math.random()*(arra.length-1))+1)]
                linkArr0.push({
                    source: a,
                    target: b
                })
                // console.log(linkArr0)
            }
              let option = {
                // backgroundColor: '#1a4377',
                tooltip: {},
                animationDurationUpdate: 1500,
                animationEasingUpdate: 'quinticInOut',
                // color:['#83e0ff','#45f5ce','#b158ff'],
                series: [
                      {
                          type: 'graph',
                          layout: 'force',
                        //   legendHoverLink:true,
                        //   focusNodeAdjacency: true,//突出顯示
                          roam: true,//鼠標滾動放大縮小
                          force:{
                              repulsion:1000,//分布范围
                              edgeLength:100//连接距离
                          },
                          
                          // roam: true,//是否移动整个容器
                          label: {
                              normal: {
                                  show: true
                              }
                          },
                          data: arr,
                          links:linkArr0,
                          lineStyle: {//连接线样式
                              normal: {
                                  opacity: 0.3,
                                  width: 3,
                                  curveness: 0,
                                  borderColor:'black',
                                  color:'black'
                              }
                          },
                          categories:[//没搞懂这个
                              {name: '0'},
                              {name: '1'},
                              {name: '2'}
                          ]
                      }
                  ]
              }
              // 绘制图表
              myChart.setOption(option);
          }
        },
    }
</script>

<style lang="less" scoped>
    #bigBox {
        color: white;
    }
    #myChart{
      position: fixed !important;
      left: 0;
      z-index: -1;
      top: 0;
    }
</style>


